import { useState } from 'react';
import { Link } from 'react-router-dom';

import NavbarBtn from '../../components/NavbarBtn';

import './Navbar.scss';

const Navbar = () => {
    const [activeNavbar, setActiveNavbar] = useState('hotDishes');
    return(
        <div className="navbar">
            <Link to="/" onClick={() => setActiveNavbar('hotDishes')} className="navbar-link">
                <NavbarBtn
                    title="Hot Dishes"
                    active={activeNavbar == 'hotDishes'}
                />
            </Link>
            <Link to="/coldDishes" onClick={() => setActiveNavbar('coldDishes')} className="navbar-link">
                <NavbarBtn
                    title="Cold Dishes"
                    active={activeNavbar == 'coldDishes'}
                />
            </Link>
            <Link to="/soup" onClick={() => setActiveNavbar('soup')} className="navbar-link">
                <NavbarBtn
                    title="Soup"
                    active={activeNavbar == 'soup'}
                />
            </Link>
            <Link to="/grill" onClick={() => setActiveNavbar('grill')} className="navbar-link">
                <NavbarBtn
                    title="Grill"
                    active={activeNavbar == 'grill'}
                />
            </Link>
            <Link to="/appetizer" onClick={() => setActiveNavbar('appetizer')} className="navbar-link">
                <NavbarBtn
                    title="Appetizer"
                    active={activeNavbar == 'appetizer'}
                />
            </Link>
            <Link to="/dessert" onClick={() => setActiveNavbar('dessert')} className="navbar-link">
                <NavbarBtn
                    title="Dessert"
                    active={activeNavbar == 'dessert'}
                />
            </Link>
        </div>
    ) 
};

export default Navbar;