import {
  BrowserRouter as Router,
  Switch,
  Route,
} from 'react-router-dom';

import HeaderTop from '../../containers/HeaderTop';
import Navbar from '../../containers/Navbar';
import Orders from '../../containers/Orders';

import HotDishes from '../../dishes/HotDishes';
import ColdDishes from '../../dishes/ColdDishes';
import Soup from '../../dishes/Soup';
import Grill from '../../dishes/Grill';
import Appetizer from '../../dishes/Appetizer';
import Dessert from '../../dishes/Dessert';

import './HomePage.scss';

const HomePage = () => {
    return (
            <div className="home-page">
                <div className="home-page-top">
                    <HeaderTop/>
                    <Router>
                        <div className="">
                            <Navbar/>
                            <div className="choose-dishes">
                                <h1>Choose dishes</h1>
                                <select name="" id="">
                                    <option value="">Dine In</option>
                                    <option value="">Salom</option>
                                    <option value="">Salom</option>
                                </select>
                            </div>
                            <Switch>    
                                <Route exact path="/" component={HotDishes}/>
                                <Route exact path="/coldDishes" component={ColdDishes}/>
                                <Route exact path="/soup" component={Soup}/>
                                <Route exact path="/grill" component={Grill}/>
                                <Route exact path="/appetizer" component={Appetizer}/>
                                <Route exact path="/dessert" component={Dessert}/>
                            </Switch>
                        </div>
                    </Router>
                </div>
                <Orders/>
            </div>
    )
};

export default HomePage;