import {
    HeartIcon,
    RestaruantIcon,
    SittingsIcon,
    AlertIcon,
    BlockIcon,
    AboutIcon
} from '../../../assets/icon/IconSittingsSidebar';

import './SittingsSidebar.scss';

const SidebarArr = [
    {
        imgLink: <HeartIcon/>,
        name: 'Appereance',
        text: 'Dark and Light mode, Font size'
    },
    {
        imgLink: <RestaruantIcon/>,
        name: 'Your Restaurant',
        text: 'Dark and Light mode, Font size'
    },
    {
        imgLink: <SittingsIcon/>,
        name: 'Products Management',
        text: 'Manage your product, pricing, etc'
    },
    {
        imgLink: <AlertIcon/>,
        name: 'Notifications',
        text: 'Customize your notifications'
    },
    {
        imgLink: <BlockIcon/>,
        name: 'Security',
        text: 'Configure Password, PIN, etc'
    },
    {
        imgLink: <BlockIcon/>,
        name: 'Security',
        text: 'Configure Password, PIN, etc'
    },
    {
        imgLink: <AboutIcon/>,
        name: 'About Us',
        text: 'Find out more about Posly'
    }
];

const SittingsSidebar = () => {
    return(
        <div className="sittings-sidebar">
            {
                SidebarArr.map(item => {
                    return(
                        <div className="sittings-sidebar-block">
                            <div className="sittings-sidebar-block-left">
                                <span>{ item.imgLink }</span>
                            </div>
                            <div className="sittings-sidebar-block-right">
                                <h4>{item.name}</h4>
                                <h5>{item.text}</h5>
                            </div>
                        </div>
                    )
                })
            }
        </div>
    )
};

export default SittingsSidebar;