import { useState } from 'react';
import { Link } from 'react-router-dom';

import SidebarBtn from '../../components/SidebarBtn';

import {
    HomeIcon,
    HourIcon,
    SettingsIcon,
    AlertIcon,
    BolakIcon,
    OutIcon,
    MassageIcon,
    LogoIcon
} from '../../assets/icon/icon';

import './Sidebar.scss';

const Sidebar = () => {

    const [activePage, setActivePage] = useState('homePage');

    return (
        <div className="sidebar">

            <div className="logo-icon">
                <LogoIcon/>
            </div>

            <Link to="/" onClick={() => setActivePage('homePage')} className="button-link">
                <SidebarBtn
                    icon={<HomeIcon/>}
                    active={activePage == 'homePage'}
                />
            </Link>
            <Link to="/hourPage" onClick={() => setActivePage('huorPage')} className="button-link">
                <SidebarBtn
                    icon={<HourIcon/>}
                    active={activePage == 'huorPage'}
                />
            </Link>
            <Link to="/bolakPage" onClick={() => setActivePage('bolakPage')} className="button-link">
                <SidebarBtn
                    icon={<BolakIcon/>}
                    active={activePage == 'bolakPage'}
                />
            </Link>
            <Link to="/massagePage" onClick={() => setActivePage('massagePage')} className="button-link">
                <SidebarBtn
                    icon={<MassageIcon/>}
                    active={activePage == 'massagePage'}
                />
            </Link>
            <Link to="/alertPage" onClick={() => setActivePage('alertPage')} className="button-link">
                <SidebarBtn
                    icon={<AlertIcon/>}
                    active={activePage == 'alertPage'}
                />
            </Link>
            <Link to="/settingsPage" onClick={() => setActivePage('settingsPage')} className="button-link">
                <SidebarBtn
                    icon={<SettingsIcon/>}
                    active={activePage == 'settingsPage'}
                />
            </Link>
            <Link to="/outPage" onClick={() => setActivePage('outPage')} className="button-link">
                <SidebarBtn
                    icon={<OutIcon/>}
                    active={activePage == 'outPage'}
                />
            </Link>
        </div>
    )
}

export default Sidebar;