import { useState } from 'react';

import Food1 from '../../../assets/images/Image1.png';
import Food2 from '../../../assets/images/Image2.png';
import Food3 from '../../../assets/images/Image3.png';
import Food4 from '../../../assets/images/Image4.png';
import Food5 from '../../../assets/images/Image5.png';
import Food6 from '../../../assets/images/Image6.png';

import './SittingsTable.scss';

const BlockArr = [
    {
        imgLink: Food1,
        text: 'Spicy seasoned seafood noodles',
        price: '$ 2.29 * 20 Bowls',
        title: 'Edit dish'
    },
    {
        imgLink: Food2,
        text: 'Spicy seasoned seafood noodles',
        price: '$ 2.29 * 20 Bowls',
        title: 'Edit dish'
    },
    {
        imgLink: Food3,
        text: 'Spicy seasoned seafood noodles',
        price: '$ 2.29 * 20 Bowls',
        title: 'Edit dish'
    },
    {
        imgLink: Food4,
        text: 'Spicy seasoned seafood noodles',
        price: '$ 2.29 * 20 Bowls',
        title: 'Edit dish'
    },
    {
        imgLink: Food5,
        text: 'Spicy seasoned seafood noodles',
        price: '$ 2.29 * 20 Bowls',
        title: 'Edit dish'
    },
    {
        imgLink: Food6,
        text: 'Spicy seasoned seafood noodles',
        price: '$ 2.29 * 20 Bowls',
        title: 'Edit dish'
    },
]

const SittingsTable = () => {

    const [ activeBtn, setActiveBtn ] = useState('hot');
    const [ activeBtn1, setActiveBtn1 ] = useState('save');

    return(
        <div className="sittings-table">
            <div className="sittings-table-top">
                <h1>Products Management</h1>
                <button>
                    <span>
                        <svg width="20" height="21" viewBox="0 0 20 21" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M15.1976 12.2271C16.927 12.2271 18.3329 13.6148 18.3329 15.3219C18.3329 17.028 16.927 18.4167 15.1976 18.4167C13.4683 18.4167 12.0624 17.028 12.0624 15.3219C12.0624 13.6148 13.4683 12.2271 15.1976 12.2271ZM15.1976 13.6787C14.279 13.6787 13.533 14.4151 13.533 15.3219C13.533 16.2277 14.279 16.9651 15.1976 16.9651C16.1162 16.9651 16.8623 16.2277 16.8623 15.3219C16.8623 14.4151 16.1162 13.6787 15.1976 13.6787ZM8.4308 14.5958C8.83667 14.5958 9.16608 14.921 9.16608 15.3216C9.16608 15.7223 8.83667 16.0474 8.4308 16.0474H2.40247C1.9966 16.0474 1.66719 15.7223 1.66719 15.3216C1.66719 14.921 1.9966 14.5958 2.40247 14.5958H8.4308ZM4.80174 2.58337C6.53112 2.58337 7.93698 3.97207 7.93698 5.67818C7.93698 7.38429 6.53112 8.77298 4.80174 8.77298C3.07334 8.77298 1.6665 7.38429 1.6665 5.67818C1.6665 3.97207 3.07334 2.58337 4.80174 2.58337ZM4.80174 4.03497C3.88411 4.03497 3.13707 4.77238 3.13707 5.67818C3.13707 6.58397 3.88411 7.32138 4.80174 7.32138C5.72035 7.32138 6.46642 6.58397 6.46642 5.67818C6.46642 4.77238 5.72035 4.03497 4.80174 4.03497ZM17.5979 4.95257C18.0038 4.95257 18.3332 5.27773 18.3332 5.67837C18.3332 6.07901 18.0038 6.40417 17.5979 6.40417H11.5686C11.1627 6.40417 10.8333 6.07901 10.8333 5.67837C10.8333 5.27773 11.1627 4.95257 11.5686 4.95257H17.5979Z" fill="white"/>
                        </svg>
                    </span>
                    <h5>Manage Categories</h5>
                </button>
            </div>
            <div className="sittings-table-navbar">
                <button onClick={() => setActiveBtn("hot")} className={activeBtn == "hot" ? 'active': ''}>Hot Dishes</button>
                <button onClick={() => setActiveBtn("cold")} className={activeBtn == "cold" ? 'active': ''}>Cold Dishes</button>
                <button onClick={() => setActiveBtn("soup")} className={activeBtn == "soup" ? 'active': ''}>Soup</button>
                <button onClick={() => setActiveBtn("grill")} className={activeBtn == "grill" ? 'active': ''}>Grill</button>
                <button onClick={() => setActiveBtn("app")} className={activeBtn == "app" ? 'active': ''}>Appetizer</button>
                <button onClick={() => setActiveBtn("deseret")} className={activeBtn == "deseret" ? 'active': ''}>Dessert</button>
            </div>
            <div className="sittings-table-blocks">
            {
                BlockArr.map(item => {
                    return(
                        <div className="sittings-table-blocks-block">
                            <img src={item.imgLink} alt=""/>
                            <h4>{item.text}</h4>
                            <h5>{item.price}</h5>
                            <button>
                                <span>
                                    <svg width="20" height="20" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg">
                                        <path d="M16.9512 16.0075C17.2543 16.0075 17.5 16.2484 17.5 16.5455C17.5 16.8178 17.2935 17.0429 17.0257 17.0785L16.9512 17.0834H11.2258C10.9227 17.0834 10.677 16.8426 10.677 16.5455C10.677 16.2731 10.8835 16.0481 11.1514 16.0124L11.2258 16.0075H16.9512ZM11.3827 3.6806C12.4217 2.66213 14.1069 2.66213 15.1459 3.6806L16.2245 4.73791C17.2635 5.75638 17.2635 7.40829 16.2245 8.42675L8.11717 16.3739C7.65359 16.8283 7.02506 17.0833 6.36901 17.0833H3.04878C2.74035 17.0833 2.49249 16.8342 2.50017 16.532L2.58369 13.248C2.6003 12.6273 2.85939 12.0355 3.30722 11.5965L11.3827 3.6806ZM10.755 5.81592L4.08332 12.3573C3.83438 12.6013 3.69012 12.9308 3.68089 13.2755L3.61121 16.0071L6.36901 16.0075C6.69352 16.0075 7.00559 15.8955 7.2519 15.6932L7.34108 15.6131L14.0458 9.04092L10.755 5.81592ZM14.3698 4.44136C13.7594 3.84305 12.7691 3.84305 12.1588 4.44136L11.5317 5.05508L14.8217 8.28008L15.4484 7.66599C16.0249 7.10092 16.0569 6.20369 15.5445 5.60171L15.4484 5.49867L14.3698 4.44136Z"/>
                                    </svg>                                
                                </span>
                                <h6>{item.title}</h6>
                            </button>
                        </div>
                    )
                })
            }
            </div>
            <div className="buttons">
                <button onClick={() => setActiveBtn1("discard")} className={activeBtn1 == 'discard' ? 'active' : ''}>Discard Changes</button>
                <button onClick={() => setActiveBtn1("save")} className={activeBtn1 == 'save' ? 'active' : ''}>Save Changes</button>
            </div>
        </div>
    )
};

export default SittingsTable;